import React from 'react'
import { Link } from 'react-router-dom'

import './ArtistTile.css'

class ArtistTile extends React.Component {

    constructor(props) {
        super(props)
        this.state = {}
    }

    render() {
        return (
            <Link to={this.props.artist.url} className="artist-tile">
                <div className="artist-img">
                    <img src={require(`../../../Images/${this.props.artist.img}`)} alt=""/>
                </div>
                <div className="artist-info">
                    <div className="artist-name">
                        {this.props.artist.name}
                    </div>
                    <div className="learn-more">Learn More &rarr;</div>
                    {/* <div className="artist-bio">
                        {this.props.bio}
                    </div> */}
                </div>
            </Link>
        )
    }
}

export default ArtistTile