import React from 'react'
import ReactDOM from 'react-dom'
import {
  BrowserRouter as Router,
  Route,
  Switch
} from 'react-router-dom'

import 'reset-css'
import './index.css'

import artistsJson from './Components/Roster/artists.json'


import Landing from './Components/Landing/Landing'
import ListingPage from './Components/Roster/ListingPage'
import ProfilePage from './Components/ProfilePage/ProfilePage'
import Footer from './Components/Footer/Footer'

ReactDOM.render(
    <>
        <Router basename={'/artists'}>
            <Switch>
                <Route exact path="/powerbase-christmas" component={() => <ProfilePage artist={artistsJson[0]}/>}/>
                <Route exact path="/sonna" component={() => <ProfilePage artist={artistsJson[1]}/>}/>
                <Route exact path="/auburn-road" component={() => <ProfilePage artist={artistsJson[2]}/>}/>

                <Route>
                    <Landing />
                    <ListingPage />
                </Route>
            </Switch>
            <Footer />
        </Router>
    </>,
    document.getElementById('root')
)