import React from 'react'
import { Link } from 'react-router-dom'

import './ProfilePage.css'

// import Image from '../../Images/ArtistImages/Sonna_BB_Clean.jpg'

class ProfilePage extends React.Component {

    constructor(props) {
        super(props)

        this.state = {
            artist: this.props.artist
        }

        window.scrollTo(0, 0)
    }

    renderContent = () => {
        switch (this.props.artist.name) {
            case 'Sonna':
                return (
                    <>
                        <div className="item">
                            <iframe title="Bad Bitch by Sonna" src="https://open.spotify.com/embed/album/0DfsFuAW9O8uL8D5MIOqNb" width="100%" height="80" frameBorder="0" allowtransparency="true" allow="encrypted-media"></iframe>
                        </div>
                        <div className="item">
                            <iframe title="Strong by Sonna" src="https://open.spotify.com/embed/track/7CMUQu028pU9oKOFMI6pgK" width="100%" height="80" frameBorder="0" allowtransparency="true" allow="encrypted-media"></iframe>
                        </div>
                    </>
                )
            case 'Auburn Road':
                return (
                    <>
                        <div className="item">
                            <iframe title="Warning by Auburn Road" src="https://open.spotify.com/embed/album/46A75UpA06kJb8df7JoVjx" width="100%" height="80" frameBorder="0" allowtransparency="true" allow="encrypted-media"></iframe>
                        </div>
                        <div className="item">
                            <iframe title="Outlaw by Auburn Road" src="https://open.spotify.com/embed/album/6507wniVWEZZnS9yyyzCpV" width="100%" height="80" frameBorder="0" allowtransparency="true" allow="encrypted-media"></iframe>
                        </div>
                        <div className="item">
                            <iframe title="Warning (Lyric Video) by Auburn Road" width="100%" height="315" src="https://www.youtube.com/embed/8oZFl7D_PZ0" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"></iframe>
                        </div>
                    </>

                )
            case "Powerbase Christmas":
                return (
                    <>
                        {/* <div className="item">
                            <img src={require('../../Images/winter_wonderland.jpg')} width="100%" alt="" />
                        </div>
                        <div className="item">
                            <img src={require('../../Images/AR_Carol.jpg')} width="100%" alt="" />
                        </div>
                        <div className="item">
                            <img src={require('../../Images/Major_You For The Holidays.jpg')} width="100%" alt="" />
                        </div>
                        <div className="item">
                            <img src={require('../../Images/Sonna_LastChristmas.jpg')} width="100%" alt="" />
                        </div> */}
                        <div className="item">
                            <iframe title="Cedric the Entertainer and Sonna Rele Spread Holiday Cheer With Impromptu Performance" width="100%" height="400" src="https://www.youtube.com/embed/R7_7yEiIkFA" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"></iframe>
                        </div>
                    </>
                )
            default: return (<></>)
        }
    }

    imageString = require(`../../Images/${this.props.artist.profile_img}`);
    render() {
        return (
            <div className="profile-page" style={this.state.style}>

                <Link to='/' className="x">X</Link>

                <div className="profile-image" style={{ backgroundImage: `url(${this.imageString})` }}></div>

                <div className="contain">
                    <div className="profile-name">{this.state.artist.name} <span>{this.state.artist.location}</span></div>
                    <div className="profile-bio">
                        <span>{this.state.artist.title}</span>
                        <div className="profile-text" dangerouslySetInnerHTML={{ __html: this.state.artist.bio }}></div>
                    </div>
                    <div className="profile-socials">
                        Find {this.state.artist.name} on<br /><br />
                        {Object.keys(this.state.artist.socials).map((name, index) =>
                            <>
                                <a key={index} href={this.state.artist.socials[name]}><i className={`fab fa-${name}`}></i></a>
                            </>
                        )}
                    </div>
                    <div className="profile-content">
                        {this.renderContent()}
                    </div>
                </div>
            </div>
        )
    }
}

export default ProfilePage