import React from 'react'
import {Link as ScrollLink} from 'react-scroll'
import Typist from 'react-typist'

import Logo from '../../Images/powerbase-logo-new.png'

import './Landing.css'

class Landing extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            typeDone: false
        }
    }

    render() {
        return (
            <div className="landing">
                <div className="contain">
                    {/* <h2>Latest from Powerbase</h2> */}
                    <img src={Logo} alt="" className="logo"/>
                    {/* <h1><a href="# " onClick={() => {window.alert("Clicking this will open Powerbase Christmas's profile.")}}>Powerbase Christmas</a> is around the corner. Watch Sonna & Cedric live on KTLA 5 TV.</h1> */}
                    <Typist cursor={{show: false}} onTypingDone={() => {
                        setTimeout(() => {
                            this.setState({
                                typeDone: true
                            })
                        },
                        500)
                    }}>
                        <h1>Innovative. Independent. Powerful.</h1>
                    </Typist>
                    {(this.state.typeDone) ? <h2>We are Powerbase.</h2> : '' }
                    <div className="buttons">
                        {/* <a href="https://www.youtube.com/watch?v=R7_7yEiIkFA" target="_blank">Watch Now</a> */}
                        <ScrollLink to="listing-page" smooth={true}>Enter</ScrollLink>
                    </div>
                </div>
                {/* <div className="overlay"></div> */}
            </div>
        )
    }
}

export default Landing